import ButtonDota from "@/components/utils/cards/ButtonDota";
import CardDota from "@/components/utils/cards/Card";
import classNames from "classnames";
import { useState } from "react";

const Links = () => {
  const [position, setPosition] = useState<"izquierda" | "derecha" | null>(
    null
  );
  return (
    <div>
      <CardDota classNames="flex flex-col gap-4 items-center">
        <div className="flex flex-col gap-2 items-center">
          <span className="text-lg">Enlace de referidos</span>
          <span className="text-[12px]">
            https://dotaacademy.com/sign-up/123456
          </span>
        </div>
        <div className="flex flex-col items-center">
          <span className="text-lg">Enlace de referidos</span>
          <CardDota classNames="!border-white  !p-0">
            <div className="flex">
              <div
                className={classNames(
                  "border-r cursor-pointer flex items-center w-full justify-center border-r-white transition ease-in-out delay-150  hover:scale-110 hover:border-r-0 duration-200 hover:border-[#DADADA] hover:-translate-x-2"
                )}
                onClick={() => setPosition("izquierda")}
              >
                <div className="p-3 px-6">
                  <span className="font-bold"> IZQUIERDA</span>
                </div>
              </div>
              <div
                className="border-l cursor-pointer flex items-center w-full justify-center border-l-white transition ease-in-out delay-150  hover:scale-110 hover:border-l-0 duration-200 hover:border-[#DADADA] hover:translate-x-2"
                onClick={() => setPosition("derecha")}
              >
                <div className="p-3 px-6">
                  <span className="font-bold"> DERECHA</span>
                </div>
              </div>
            </div>
          </CardDota>
        </div>
        <CardDota classNames="flex items-center justify-center w-full">
          {position ? (
            <span>
              Colocaremos al invitado que se registre con esta landing a tu lado{" "}
              {position}
            </span>
          ) : (
            <span>Elige la colocación más tarde</span>
          )}
        </CardDota>
        <div className="flex gap-4 w-full">
          <ButtonDota
            rounded="lg"
            classNames="w-full bg-primary font-bold hover:bg-secondary"
          >
            COPIAR
          </ButtonDota>
          <ButtonDota
            rounded="lg"
            classNames="w-full bg-primary font-bold hover:bg-secondary"
          >
            ENVIAR
          </ButtonDota>
        </div>
      </CardDota>
    </div>
  );
};

export default Links;
